/* MEDIA QUERY */
.suggestions li {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  list-style-type: none;
  padding: 10px;
  border-bottom: solid 1px #ccc; }

.suggestions__photo {
  width: 70px;
  height: 70px;
  min-width: 70px;
  border-radius: 50%;
  background-color: #ccc;
  margin-right: 10px; }

.suggestions__testo h3 {
  margin: 0; }

.suggestions__testo p {
  font-size: 14px; }



